.navbar{
    width: 100%;
    height: 100px;
    color: white;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: fixed;
    z-index: 10;
    text-decoration: none;
    list-style: none;
}
.links{
    display: flex;
    list-style: none; 
}
.links li{
    display: block;
    margin-right: 30px;
    cursor: pointer;
    width: inherit;
    text-decoration: none;
}
.links li a:hover::after,.links li a:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    color: orange;
}
.links li a{
    color: white;
    text-decoration: none;
}
.links li a::after{
    content: '';
    display: block;
    height: 2px;
    background-color: orange;
    transition: all 0.7s ease;
    -webkit-transform: scale(0);
            transform: scale(0);
    margin-top: 4px;
    color: orange;
}
.links li.active a::after{
    -webkit-transform: scale(1);
            transform: scale(1);
}
.links li.active a{
    color: orange;
}
.first,.second,.third{
    width: 25px;
    height: 4px;
    border-radius: 2px;
    background-color: white;
    margin-bottom: 3px;
}
